* {
  margin: 0;
  padding: 0;
}

:root {
  --borderColor: #c1c1c1;
  --color1-1: white;
  --color1-2: white;
  --color1-3: white;
  --color2-1: white;
  --color2-2: white;
  --color2-3: white;
  --color3-1: white;
  --color3-2: white;
  --color3-3: white;
  --color4-1: white;
  --color4-2: white;
  --color4-3: white;
}

@font-face {
  font-family: Bangers;
  src: url("Bangers-Regular.2721d9e4.ttf") format("truetype");
}

html, body {
  background-color: #000;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: table;
}

h2 {
  font-size: 1.5rem;
}

.hidden {
  opacity: 0;
}

section {
  background-color: #efefef;
  border-bottom: 1px solid #000;
  min-height: 100vh;
}

.container {
  margin-top: 20px;
}

.flex {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.flex-row {
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.full-wrapper {
  justify-content: center;
  align-items: center;
  height: 100%;
}

.text {
  color: wheat;
  text-align: center;
  width: 50vw;
  font-family: Bangers;
  font-size: 3em;
}

.text-bounce-right, .text-bounce-left {
  opacity: 0;
  animation-duration: .8s;
  animation-delay: .3s;
  animation-fill-mode: forwards;
}

.text-bounce-right {
  animation-name: text-bounce-right;
}

.text-bounce-left {
  animation-name: text-bounce-left;
}

.delay {
  animation-delay: .5s;
}

.name {
  color: #0ff;
  display: inline;
}

.grow {
  transition: all .2s ease-in-out;
}

.grow:hover {
  transform: scale(1.1);
}

.button {
  color: wheat;
  border: 3px solid wheat;
  border-radius: 50%;
  margin-top: 1em;
  padding: 15px;
  font-size: 3rem;
}

.button:hover {
  color: #000;
  background: wheat;
}

.row {
  justify-content: center;
}

#home {
  background: #000 url("bg_mobile.75c6f2f6.webp") 0 0 / cover;
  height: 100vh;
}

#about {
  background-size: 300%;
  animation: 25s infinite bg-animation;
}

section > h1 {
  margin-top: 20px;
}

.about-container {
  border: 1px solid var(--borderColor);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 40px;
  font-size: 1.2em;
  transition: all .2s ease-in-out;
  overflow: hidden;
  box-shadow: -2px 7px 21px -9px #000000bf;
}

.about-container:hover {
  transform: scale(1.005);
}

.about-container img {
  margin: 0 0 10px;
}

#skills {
  background-size: 300%;
  animation: 25s infinite bg-animation;
}

.skill {
  margin: 15px;
}

.img-wrapper {
  align-items: center;
  height: 100%;
  display: flex;
}

#work {
  background-size: 300%;
  animation: 25s infinite bg-animation;
}

.skill, .work {
  justify-content: start;
  min-height: 300px;
  margin-top: 10px;
}

.skill:hover, .work:hover {
  transform: scale(1.02);
}

.skill-wrapper, .work-wrapper {
  border: 1px solid var(--borderColor);
  background: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 200px;
  padding: 15px;
  display: flex;
  position: relative;
  box-shadow: -2px 7px 21px -9px #000000bf;
}

.skill-wrapper {
  justify-content: flex-end;
  min-width: 200px;
}

.skill-wrapper > .img-container {
  place-content: stretch space-evenly;
  align-items: center;
  height: 100%;
  display: flex;
}

.skill-wrapper > img {
  max-height: unset;
  max-width: unset;
  width: 100px;
  margin-bottom: 10px;
}

.wide-img > img {
  width: 150px;
  margin-top: 20px;
  margin-bottom: 0;
}

.wide-img > h2 {
  margin-bottom: 10px;
}

.workfor {
  font-weight: 700;
}

.logo {
  align-items: center;
  min-height: 50px;
  max-height: 200px;
  margin: 15px;
  display: flex;
}

.logo > img {
  max-height: 200px;
}

.detail {
  opacity: 0;
  border: 1px solid var(--borderColor);
  background-color: #fff;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: fit-content;
  height: fit-content;
  min-height: 105%;
  padding: 15px;
  display: flex;
  position: absolute;
  inset: 0;
  box-shadow: -2px 7px 21px -9px #000000bf;
}

.work:hover {
  z-index: 2;
}

.work:hover .logo {
  opacity: 0;
}

.work:hover .detail {
  opacity: 1;
}

.tags {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 10px;
  display: flex;
}

.tag {
  color: #fff;
  background-color: #35679e;
  border-radius: 10px;
  margin: .2em;
  padding: 1px 10px;
}

.tag.selected {
  background-color: #7d03a9;
}

#contact {
  background-size: 300%;
  animation: 25s infinite bg-animation;
}

.social-media {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.social-media img {
  width: 150px;
}

.social-media p {
  white-space: nowrap;
  margin-top: 2em;
}

.social-media p a {
  color: #777;
  font-size: 18px;
  line-height: 1.4;
}

.fa-github {
  color: #000;
}

@keyframes bg-animation {
  0% {
    background-position: 0;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0;
  }
}

h1 {
  color: #000;
  font-family: Raleway, sans-serif;
  font-size: 2.5rem;
  font-weight: bold;
}

.header-underline {
  background: #000;
  width: 15em;
  height: 4px;
  margin: 15px 0 20px;
}

@keyframes text-bounce-right {
  0% {
    opacity: .2;
    transform: translateX(-200px);
  }

  60% {
    opacity: .7;
    transform: translateX(15px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes text-bounce-left {
  0% {
    opacity: .2;
    transform: translateX(200px);
  }

  60% {
    opacity: .7;
    transform: translateX(-15px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes button-bounce-rotate {
  0% {
    opacity: .2;
    transform: perspective(800px)rotateX(-80deg);
  }

  20% {
    opacity: 1;
  }

  60% {
    transform: perspective(800px)rotateX(20deg);
  }

  100% {
    opacity: 1;
    transform: perspective(800px)rotateX(0);
  }
}

nav {
  color: #fff;
  text-align: left;
  z-index: 99;
  background: #1b242f;
  border-bottom: 3px solid #f0130b;
  width: 100%;
  height: 50px;
  position: absolute;
  left: 0;
}

nav.fixed {
  bottom: inherit;
  animation: .5s scrollIn;
  position: fixed;
  top: 0;
  left: 0;
}

nav .nav-wrapper {
  background: #333;
  width: 100%;
  max-width: 1200px;
  height: 0;
  transition: height .3s ease-out;
  position: absolute;
  top: 50px;
  overflow: hidden;
}

nav .nav-wrapper.visible {
  height: 260px;
}

nav .nav-wrapper div {
  cursor: pointer;
  text-transform: uppercase;
  padding: 12px 20px;
  font-size: 12pt;
  transition: color .5s;
}

nav .nav-wrapper div:first-child {
  padding: 25px 20px 12px;
}

nav .nav-wrapper div:hover {
  color: #fed128;
}

nav div.icon {
  color: #fff;
  position: absolute;
  top: 8px;
  right: 15px;
}

nav .active {
  color: #fed128;
}

@media screen and (width >= 600px) {
  #home {
    background: url("bg.9566d06b.webp");
  }

  nav .nav-wrapper {
    height: initial;
    position: initial;
    text-align: left;
    background: none;
    overflow: visible;
  }

  nav .nav-wrapper .visible {
    height: initial;
  }

  nav .nav-wrapper div {
    font-size: initial;
    padding: initial;
    margin: 0 20px;
    display: inline;
  }

  nav .nav-wrapper div:first-child {
    padding: 0;
  }

  nav .icon {
    display: none;
  }

  .detail {
    width: min(125%, 90vw);
    min-height: 105%;
  }

  .work:hover .detail {
    translate: -10% -5%;
  }
}

@keyframes scrollIn {
  0% {
    transform: translateY(-100px);
  }
}

img {
  max-width: 100%;
  max-height: 100%;
}
